import React, { Component } from 'react';
import {Table, Icon, Form, Button, Modal, message} from 'antd';
import exportSheet from '../../utils/exportSheet';
import commonUrl from '../../api/commonUrl';
import orderMgtUrl from '../../api/orderMgtUrl';
import utils from '../../utils/utils';
import Storage from '../../utils/localStorage';

import BlSelect from '../Common/SearchForm/BlSelect';
import BlInput from '../Common/SearchForm/BlInput';
import BlSelectModelStoreTable from '../Common/SearchForm/BlSelectModelStoreTable';
import BlDatePicker from '../Common/SearchForm/BlDatePicker';

import './style.less';
const { confirm } = Modal;
const yVal = document.body.clientHeight - 210;
class SearchOrder extends Component {
  constructor(props) {
    super(props);
    this.state = {
      formLayout: 'vertical',
      pageNum: 1,
      pageSize: 10,
      loading: false,
      exportLoading: false,
      total: 0,
      columns: [
        {
          title: '订单编号',
          dataIndex: 'paperNo',
          width: 160
        },
        {
          title: '供应商',
          dataIndex: 'vendorName',
          width: 130
        },
        {
          title: '城市',
          dataIndex: 'cityName',
          width: 80
        },
        {
          title: '订货门店/仓库',
          dataIndex: 'storeName',
          width: 140
        },
        {
          title: '联系人',
          dataIndex: 'createUserContact',
          width: 115
        },
        {
          title: '单据时间',
          dataIndex: 'authDatetime',
          width: 170
        },
        {
          title: '期望到货时间',
          dataIndex: 'expectDatetime',
          width: 170
        },
        {
          title: '状态',
          dataIndex: 'statusDesc',
          width: 80
        },
        {
          title: '订单金额',
          dataIndex: 'totalAmount',
          width: 80
        },
        {
          title: '订单数量',
          dataIndex: 'totalQty',
          width: 80
        },
        {
          title: '订单商品数',
          dataIndex: 'totalGoodsQty',
          width: 90
        },
        {
          title: '收货金额',
          dataIndex: 'totalReceiveAmount',
          width: 80
        },
        {
          title: '收货数量',
          dataIndex: 'totalReceiveQty',
          width: 80
        },
        {
          title: '收货商品数',
          dataIndex: 'totalReceiveGoodsQty',
          width: 90
        },
        {
          title: '是否查看',
          dataIndex: 'isOpenDes',
          width: 80
        },
        {
          title: '备注',
          dataIndex: 'remark',
          render: (text) => (
            <div style={{ wordWrap: 'break-word', wordBreak: 'break-word' }}>
              {text}
            </div>
          )
        },
        {
          title: '操作',
          fixed: 'right',
          width: 140,
          render: (item) => <div>
            {item.paperNo ? <div>
              <Button type="link" onClick={this.goDetailPage.bind(this, item)}><Icon type="eye" />详情</Button>
              {item.status === 0 ? <Button type="link" onClick={this.ordersHandel.bind(this, item)}><Icon type="pushpin" />接单</Button> : null}
            </div> : null}
          </div>
        }
      ],
      tableData: [],

      // 供应商
      vendorNo: [],
      venderOptions: [],

      paperNo: '', // 订单编码

      // 门店编码
      storeName: '',
      storeNo: [],
      
      startDatetime: utils.dateFormat('yyyy-MM-dd', Date.parse(new Date()) - 7 * 86400000), 
      endDatetime: utils.dateFormat('yyyy-MM-dd', Date.parse(new Date())),
      // 是否查看
      isOpen: -1, // -1 全部
      isOpenOptions: [
        {
          label: '全部',
          value: -1
        },
        {
          label: '已查看',
          value: 1
        },
        {
          label: '未查看',
          value: 0
        }
      ],
      status: -1, // -1 全部
      statusOptions: [
        {
          label: '全部',
          value: -1
        },
        {
          label: '待接单',
          value: 0
        },
        {
          label: '待收货',
          value: 1
        },
        {
          label: '已收货',
          value: 2
        },
        {
          label: '已取消',
          value: 3
        },
        {
          label: '拒绝接单',
          value: 4
        },
        {
          label: '已违约',
          value: 5
        }
      ],
      exportConfig: {
        paperNo: '订单编号',
        vendorName: '供应商',
        cityName: '城市',
        storeName: '订货门店/仓库',
        createUserContact: '联系人',
        authDatetime: '单据时间',
        expectDatetime: '期望到货时间',
        statusDesc: '状态',
        totalAmount: '订单金额',
        totalQty: '订单数量',
        totalGoodsQty: '订单商品数',
        totalReceiveAmount: '收货金额',
        totalReceiveQty: '收货数量',
        totalReceiveGoodsQty: '收货商品数',
        isOpenDes: '是否查看',
        remark: '备注'
      },

      checkedList: []
    }
  }

  componentDidMount() {
    // 获取供应商列表
    this.getVenderList()

    let searchContentMsg = Storage.get("searchContent")
    if(searchContentMsg) {
      this.setState({...searchContentMsg}, () => {
        this.doSearchHandel()
      })
    }else {
      this.doSearchHandel()
    }
  }

  render() {
    let {columns, tableData, checkedList} = this.state
    const rowSelection = {
      onChange: this.onChange
    }
    
    return (
      <div className="main-container">
        <div className="search-wraper">
          <Form layout={this.state.formLayout}>
            <BlSelect label="供应商" placeholder="选择供应商" mode="multiple" name="vendorNo" maxTagCount={1} maxTagTextLength={2} defaultVal={this.state.vendorNo} optionsMap={this.state.venderOptions} formItemChange={this.formItemChange} />
            <BlSelectModelStoreTable label="门店" placeholder="点击选择门店" vendorNo={this.state.vendorNo} venderOptions={this.state.venderOptions} confirmHandel={this.confirmHandel} defaultVal={this.state.storeName} />
            <BlInput label="订单编号" name="paperNo" defaultVal={this.state.paperNo} formItemChange={this.formItemChange}/>
            <BlSelect label="状态" placeholder="选择状态" name="status" defaultVal={this.state.status} optionsMap={this.state.statusOptions} formItemChange={this.formItemChange} />
            <BlDatePicker label="单据开始时间" allowClear={true} defaultVal={this.state.startDatetime} name="startDatetime" formItemChange={this.formItemChange}/>
            <BlDatePicker label="单据结束时间" allowClear={true} defaultVal={this.state.endDatetime} name="endDatetime" formItemChange={this.formItemChange}/>
            <BlSelect label="是否查看" placeholder="是否查看" name="isOpen" defaultVal={this.state.isOpen} optionsMap={this.state.isOpenOptions} formItemChange={this.formItemChange} />
            <Form.Item
              className="btn-wrap"
              label="&nbsp; ">
              <Button type="primary" onClick={this.doSearchHandel.bind(this, true)}><Icon type="search" />查询</Button>
              <Button onClick={this.doResetHandel.bind(this)}><Icon type="reload" />重置</Button>
              <Button onClick={this.doExportDataHandel.bind(this)} disabled={this.state.exportLoading || checkedList.length === 0}>{this.state.exportLoading ? <Icon type="loading" /> : <Icon type="download" />}导出</Button>
            </Form.Item>
          </Form>
        </div>
        <div className="content-wraper">
          <Table 
            loading={this.state.loading}
            rowKey={item => item.paperNo}
            columns={columns} 
            dataSource={tableData} 
            size="middle" 
            rowSelection={rowSelection}
            pagination={
              {
                size: "middle",
                pageSizeOptions: ['10', '50', '100', '150', '200'],
                showSizeChanger: true,
                showQuickJumper: true,
                defaultCurrent: this.state.pageNum,
                total: this.state.total,
                onShowSizeChange: this.onPageSizeChange,
                onChange: this.pageNumChange,
                showTotal: this.showTotal
              }
            }
            scroll={{ x: 1960, y: yVal }}/>
        </div>
      </div>
        
    )
  }

  onChange = (record, selected) => {
    this.setState({
      checkedList: [...selected]
    })
  }

  formItemChange = (result) => {
    this.setState({
      [result.name]: result.value
    })
  }

  confirmHandel = (result) => {
    this.setState({
      storeNo: result.storeNo,
      storeName: result.storeName
    })
  }

  onPageSizeChange = (current, pageSize) => {
    this.setState({
      pageSize: pageSize
    }, () => {
      this.doSearchHandel()
    })
  }

  pageNumChange = (pageNumber) => {
    this.setState({
      pageNum: pageNumber
    }, () => {
      this.doSearchHandel()
    })
  }

  showTotal = () => {
    return `共 ${this.state.total} 条`;
  }
  
  doSearchHandel = (param) => {
    let {pageNum, pageSize, vendorNo: vendorNos, storeNo: storeNos, paperNo, isOpen, status, startDatetime, endDatetime} = this.state
    if(param) {
      pageNum = 1
    }
    this.setState({
      loading: true
    })
    let _params = {pageNum,pageSize,vendorNos,storeNos,paperNo,isOpen,status,startDatetime,endDatetime}
    this.$http.post(orderMgtUrl.orderList, {..._params}).then(res => {
      this.setState({
        loading: false
      })
      if(res.code === 0) {
         let tableDataList = res.data && res.data.result && res.data.result.length ? res.data.result : []
         let tableData = tableDataList.map(item => {
          item.isOpenDes = item.isOpen ? '已查看' : '未查看'
          return item 
         })
        this.setState({
          tableData: tableData,
          total: res.data.total
        })
        return
      }
      throw new Error(res.msg)
    }).catch(e => {
      this.setState({
        loading: false
      })
      utils.openNotification('error', '系统提示', e.message || '')
    })
  }

  doExportDataHandel = () => {
    let checkedList = this.state.checkedList
    if(checkedList && checkedList.length) {
      let paperNos = checkedList.map(item => {return item.paperNo}).join(",")
      this.setState({
        exportLoading: true
      })
      this.$http.post(orderMgtUrl.orderOpen, {opType: 202, paperNo: paperNos}).then(res => {
        this.setState({
          exportLoading: false
        })
        if(res.code === 0) {
          if(res.data && res.data.result) {
            exportSheet.exportFileByConfig(checkedList, '订单汇总.xlsx', this.state.exportConfig)
          }else {
            message.error('导出订单置为已查看状态失败');
          }
          return
        }
        throw new Error(res.msg)
      }).catch(e => {
        this.setState({
          exportLoading: false
        })
        utils.openNotification('error', '系统提示', e.message || '')
      })

    }else {
      utils.openNotification('warning', '系统提示', '没有查询到可导出数据')
    }
  }

  getVenderList = () => {
    let params = {
      requireVendorName: true,
      userNo: Storage.get('loginInfo') ? Storage.get('loginInfo').UserNO : ''
    }
    this.$http.post(commonUrl.vendorList, {...params}).then(res => {
      if(res.code === 0) {
        let venderOptionsArr = res.data && res.data.length ? res.data.map((item) => {
          let newItem = {
            id: item.vendorNo,
            value: item.vendorNo,
            label: item.vendorName
          }
          return newItem
        }) : []
        this.setState({
          venderOptions: venderOptionsArr
        })
        return
      }
      throw new Error(res.msg);
    }).catch(e => {
      console.log("【供应商提示】:", e.message)
    })
  }
  
  goDetailPage = (_row) => {
    let {vendorNo, paperNo, storeNo, storeName, startDatetime, endDatetime, isOpen, status} = this.state
    let searchContent = {vendorNo, paperNo, storeNo, storeName, startDatetime, endDatetime, isOpen, status}
    Storage.set("orderMsg", _row)
    Storage.set("searchContent", searchContent)
    const {history} = this.props
    history.push(`/order_mgt/${_row.paperNo}`)
  }

  ordersHandel = (_row) => {
    let _this = this
    confirm({
      title: '是否接受该订单？',
      okText: '接受订单',
      cancelText: '拒绝接单',
      onOk() {
        _this.$http.post(orderMgtUrl.acceptOrders, {opType: 201, paperNo: _row.paperNo}).then(res => {
          if(res.code === 0) {
              if(res.data && res.data.result) {
                message.success('接受订单成功');
              }else {
                message.error('接受订单失败');
              }
            return
          }
          throw new Error(res.msg)
        }).catch(e => {
          utils.openNotification('error', '系统提示', e.message || '')
        })
      },
      onCancel() {
        _this.$http.post(orderMgtUrl.acceptOrders, {opType: 203, paperNo: _row.paperNo}).then(res => {
          if(res.code === 0) {
              if(res.data && res.data.result) {
                message.success('拒绝订单成功');
              }else {
                message.error('拒绝订单失败');
              }
            return
          }
          throw new Error(res.msg)
        }).catch(e => {
          utils.openNotification('error', '系统提示', e.message || '')
        })
      },
    });
  }

  doResetHandel = () => {
    this.setState({
      // 供应商
      vendorNo: [],
      paperNo: '', // 订单编码
      // 门店编码
      storeName: '',
      storeNo: [],
      // 订单开始日期
      startDatetime: null, 
      // 订单结束日期
      endDatetime: null, 
      // 是否查看
      isOpen: -1, // -1 全部
      status: -1, // -1 全部
      pageNum: 1,
      pageSize: 10,
      total: 0,
      tableData: []
    }, () => {
      this.doSearchHandel(true)
    })
  }
}

export default SearchOrder;